.formInput {
  background-color: #37363a;
  width: 70%;
  height: 50px;
  border: 1px solid #e13234;
  border-radius: 16px;
  padding: 0px 45px 0px 15px;
  font-size: 20px;
  font-family: Termina;
  font-weight: 900;
  color: #ffffff;
  margin-bottom: 0.5rem;
}

.submitButton {
  background-color: #e13234;
  color: white;
  border: none;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  display: center;
  font-size: 15px;
  font-family: Termina;
  cursor: pointer;
  border-radius: 16px;
  position: center;
}
